
.section1{
  display: flex;
padding: 86px 10% 0;
width: 100%;
align-items: center;
min-height: 100vh;
justify-content: space-between;
}

.section1 .container{
  max-width: 600px;
  margin: 20px 0;
}
.section1 .container h3{
  font-size: 32px;
  font-weight: 700;
  opacity: 0;
  animation: slidebottom 1s ease forwards;
  animation-delay:.7s ;
}
.section1 .container h3 span{
  color: #0ef;
}
.section1 .container h3:nth-of-type(2){
  margin: 0 0 30px 0;
  animation: slidetop 1s ease forwards;
  animation-delay:.7s ;
}
.section1 .container h1{
  font-size: 56px;
  font-weight: 700;
  margin: 3px 0;
  opacity: 0;
  animation: slideright 1s ease forwards;
  animation-delay:1s ;
  line-height: 60px;
}
.section1 .container p{
  font-size: 16px;
  animation: slideleft 1s ease forwards;
  animation-delay: 1s;
  opacity: 0;
}
.section1 .image-container{
  margin: 0 0 0 15px;
}
.section1 .image-container .image-holder{
  width: 320px;
}
.section1 .image-container .image-holder img{
  width: 100%;
  border-radius: 50%;
  opacity: 0;
  animation: zoomin 1s ease forwards , updownimg 4s ease-in-out infinite;
  animation-delay:3s ;
}

.social-div a{
 display: inline-flex;
 justify-content: center;
 align-items: center;
 width: 40px;
 height: 40px;
 color: #0ef;
 border: 2px solid #0ef;
 border-radius: 50%;
 font-size: 20px;
 background: transparent;
 text-decoration: none;
 margin: 30px 15px 30px 0;
 transition: .5s ease;
 opacity: 0;
  animation: slideleft 1s ease forwards;
  animation-delay:calc(.2s * var(--i));
}
.social-div a:hover{
  color: #1f242d;
  background-color: #0ef;
  box-shadow: 0px 0px 5px #0ef;
}



.service-div{
  background-image: url('./img/h2-bg-section-02.webp');
  background-repeat: no-repeat;
    background-size: 150%;
    background-position-x: -316px;
}
.experinceimage{
  background-image: url('./img/testomonal.jpg');
  background-repeat: no-repeat;
    background-size:cover;

    
}

.projectbg{
  background-image: url('./img/Rectangle\ 94.jpg');
 
  background-position-x: 743px;
  background-repeat: no-repeat;
  /* border: 6px solid #BC7AFF; */
  
}

.contact-each-box:hover{
  color: white;
  background-color: #5333F2;
  transition: all .4s;
}
.contact-each-box{
  transition: all .4s;
}
.contact-each-box:hover h1 ,.contact-each-box:hover  i{
  color: white;
}

.spin{
  animation: spin 1.4s ease-in infinite;
}

@keyframes spin{

  0%{
    rotate: 0deg;
  }
  50%{
    rotate: 180deg;
  }
  100%{
    rotate:0deg;
  }
}

@keyframes slideright{
  0%{
      transform: translateX(-100px);
      opacity: 0;
  }
  100%{
      transform: translateX(0px);
      opacity: 1;
  }
}
@keyframes slideleft{
  0%{
      transform: translateX(100px);
      opacity: 0;
  }
  100%{
      transform: translateX(0px);
      opacity: 1;
  }
}
@keyframes slidetop{
  0%{
      transform: translateY(100px);
      opacity: 0;
  }
  100%{
      transform: translateY(0px);
      opacity: 1;
  }
}
@keyframes slidebottom{
  0%{
      transform: translateY(-100px);
      opacity: 0;
  }
  100%{
      transform: translateY(0px);
      opacity: 1;
  }
}
@keyframes zoomin{
  0%{
      transform:scale(0);
      opacity: 0;
  }
  100%{
      transform:scale(1);
      opacity: 1;
  }
}
@keyframes updownimg{
  0%{
      transform: translateY(0px);
      
  }
  50%{
      transform: translateY(-25px);

  }
  100%{
      transform: translateY(0px);

  }
}

